body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 100%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.stopwatch__minutes-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation-name: hand-animate;
  animation-duration: 3600s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-direction: normal;
  position: absolute;
}

.stopwatch__seconds-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation-name: hand-animate;
  animation-duration: 60s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-direction: normal;
  position: absolute;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 100%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch--speed-up .stopwatch__minutes-hand {
  transform-origin: bottom;
  animation-name: hand-animate;
  animation-duration: 600s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-direction: normal;
  position: absolute;
}

.stopwatch--speed-up .stopwatch__seconds-hand {
  transform-origin: bottom;
  animation-name: hand-animate;
  animation-duration: 10s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  animation-direction: normal;
  position: absolute;
}

@keyframes hand-animate {
  from {
    transform: translateY(-50%)rotate(0);
  }

  to {
    transform: translateY(-50%)rotate(360deg);
  }
}

